/**
 *
 * @param {*} data
 */
export function main(data) {
  // const svg = d3.select('#vizualization-svg')
  // var dataset1 = [
  //   [1, 1], [12, 20], [24, 36],
  //   [32, 50], [40, 70], [50, 100],
  //   [55, 106], [65, 123], [73, 130],
  //   [78, 134], [83, 136], [89, 138],
  //   [100, 140]
  // ]
  console.log(data)
  var svg = d3.select('#vizualization-svg2')
  var margin = 200
  // var width = svg.attr('width') - margin
  // var height = svg.attr('height') - margin
  var width = margin
  var height = margin
  const xScale = d3.scaleTime()
    .domain([data.minX, data.maxX])
    .range([0, glob.sizes.vizSvgSizes.innerWidth])
    const yScale = d3.scaleLinear()
    .domain([data.minY, data.maxY])
    .range([glob.sizes.vizSvgSizes.innerHeight, 0])

  // Title
  svg.append('text')
    .attr('x', width / 2 + 100)
    .attr('y', 100)
    .attr('text-anchor', 'middle')
    .style('font-family', 'Helvetica')
    .style('font-size', 20)
    .text('Smokers spend')

  // X label
  svg.append('text')
    .attr('x', width / 2 + 100)
    .attr('y', height - 15 + 150)
    .attr('text-anchor', 'middle')
    .style('font-family', 'Helvetica')
    .style('font-size', 12)
    .text('Time')

  // Y label
  svg.append('text')
    .attr('text-anchor', 'middle')
    .attr('transform', 'translate(60,' + height + ')rotate(-90)')
    .style('font-family', 'Helvetica')
    .style('font-size', 12)
    .text('Price')

  svg.append('g')
    .attr('transform', 'translate(0,' + height + ')')
    .call(d3.axisBottom(xScale))

  svg.append('g')
    .call(d3.axisLeft(yScale))
  svg.append('g')
    .selectAll('dot')
    .data(data)
    .enter()
    .append('circle')
    .attr('cx', function (d) { return xScale(d[0]) })
    .attr('cy', function (d) { return yScale(d[1]) })
    .attr('r', 2)
    .attr('transform', 'translate(' + 100 + ',' + 100 + ')')
    .style('fill', '#CC0000')

  var line = d3.line()
    .x(function (d) { return xScale(d[0]) })
    .y(function (d) { return yScale(d[1]) })
    .curve(d3.curveMonotoneX)

  svg.append('path')
    .datum(data)
    .attr('class', 'line')
    .attr('transform', 'translate(' + 100 + ',' + 100 + ')')
    .attr('d', line)
    .style('fill', 'none')
    .style('stroke', '#CC0000')
    .style('stroke-width', '2')

  $('#slider-range').slider({
    range: true,
    min: 0,
    max: 500,
    values: [75, 300],
    slide: function (event, ui) {
      $('#amount').val('$' + ui.values[0] + ' - $' + ui.values[1]);
    }
  });
  $('#amount').val('$' + $('#slider-range').slider('values', 0) +
    ' - $' + $('#slider-range').slider('values', 1))
}
